.page {
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: auto;
}

.page__section{
    width: 100vw;
    height: 100vh;
    scroll-snap-align: start;
    padding-top: 10%;
}

@media screen and (max-width: 480px) {
    .page__section {
        padding-top: 7vh;
    }
}