.portfolio__grid {
    margin-left: 10%;
    margin-right: 10%;
    margin-top: 5vh;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 80%;
    gap: 0 5rem;
}

.portfolio__grid> :nth-child(odd) {
    margin-top: 5rem;
}

.portfolio__item img {
    width: 100%;
    height: 315px;
    object-fit: cover;
    background-color: black;
}

.portfolio__item img:hover {
    transform: scale(1.1);
}

.portfolio__item p {
    font-weight: bold;
    text-align: center;
}

@media (max-width: 768px) {
    .portfolio__grid {
        grid-template-columns: repeat(1, 1fr);
        gap: 3rem 0;
    }

    .portfolio__grid> :nth-child(odd) {
        margin-top: 0;
    }
}