.navbar {
    display: flex;
    flex-direction: row;
    justify-content: center;
    height: 7vh;
    padding-bottom: 2vh;
    position: fixed;
    right: 3vh;
    border-bottom-left-radius: 3vh;
    border-bottom-right-radius: 3vh;
    top: 0;
    gap: 10px;
    background-color: navy;
    width: 60vh;
}

.theme button,
.navbar button {
    padding: 5px;
    padding-left: 10px;
    padding-right: 10px;
    border-bottom-right-radius: 15px;
    border-bottom-left-radius: 15px;
    border: none;
    cursor: pointer;
    transition: all 0.5s;
}

@media screen and (max-width: 480px) {
    .navbar {
        right: 10px;
        width: 100%;
        border-radius: 0px;
        right: 0px;
    }
}
