* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
:root {
    --text-color: white;
    --background-color: #383838;
  }
  
body {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    background-color: var(--background-color);
    color: var(--text-color);
  }

.title {
    font-size: 50px;
    display: block;
    text-align: center;
    font-weight: bold;
}

@media screen and (max-width: 480px){
  .title {
    font-size: 10vw;
  }
}
  