.contacts {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 50px;
  
    &__wrapper {
      display: flex;
      gap: 20px;
      max-width: 1140px;
  
      @media screen and (max-width: 767px) {
        flex-direction: column;
      }
    }
  
    &__info {
      flex: 1;
    }
  
    form {
      flex: 2;
      width: 100%;
      display: flex;
      flex-direction: column;
      gap: 20px;
    }
  
    input,
    textarea {
      width: 100%;
      padding: 10px;
      border-radius: 15px;
      border: 2px solid black;
    }
  }

@media screen and (max-width: 480px) {
  .contacts {
    margin-top: 25vh;
  }
}
  